/**
 * Theme management module for handling theme switching, persistence, and system theme detection
 * Based on the original implementation from Sam's Portfolio Website
 *
 * AI-OPTIMIZED: Includes troubleshooting comments and robust error handling for theme and accessibility.
 */

export class ThemeManager {
  constructor() {
    // Preserve existing theme state
    this.theme = localStorage.getItem('theme') || this.getSystemTheme();
    this.supportsReducedMotion = window.matchMedia(
      '(prefers-reduced-motion: reduce)'
    ).matches;
    this.supportsHighContrast = window.matchMedia(
      '(prefers-contrast: high)'
    ).matches;

    // Initialize theme system
    try {
      this.init();
    } catch (err) {
      // TROUBLESHOOTING: ThemeManager failed to initialize
      console.error('ThemeManager init error:', err);
    }
  }

  init() {
    // Set initial theme
    this.setTheme(this.theme);

    // Initialize theme toggle
    const themeToggle = document.querySelector('[data-theme-toggle]');
    if (themeToggle) {
      themeToggle.addEventListener('click', () => {
        try {
          this.toggleTheme();
        } catch (err) {
          // TROUBLESHOOTING: Theme toggle failed
          console.error('Theme toggle error:', err);
        }
      });
    }

    // Listen for system theme changes
    try {
      window
        .matchMedia('(prefers-color-scheme: dark)')
        .addEventListener('change', e => {
          if (!localStorage.getItem('theme')) {
            this.setTheme(e.matches ? 'dark' : 'light');
          }
        });
    } catch (err) {
      // TROUBLESHOOTING: System theme detection failed
      console.warn('System theme detection error:', err);
    }

    // Listen for reduced motion changes
    try {
      window
        .matchMedia('(prefers-reduced-motion: reduce)')
        .addEventListener('change', e => {
          this.supportsReducedMotion = e.matches;
          document.documentElement.classList.toggle('reduced-motion', e.matches);
        });
    } catch (err) {
      // TROUBLESHOOTING: Reduced motion detection failed
      console.warn('Reduced motion detection error:', err);
    }

    // Listen for high contrast changes
    try {
      window
        .matchMedia('(prefers-contrast: high)')
        .addEventListener('change', e => {
          this.supportsHighContrast = e.matches;
          document.documentElement.classList.toggle('high-contrast', e.matches);
        });
    } catch (err) {
      // TROUBLESHOOTING: High contrast detection failed
      console.warn('High contrast detection error:', err);
    }

    // Set initial accessibility classes
    document.documentElement.classList.toggle(
      'reduced-motion',
      this.supportsReducedMotion
    );
    document.documentElement.classList.toggle(
      'high-contrast',
      this.supportsHighContrast
    );
  }

  getSystemTheme() {
    // TROUBLESHOOTING: If this fails, check browser support for matchMedia
    return window.matchMedia('(prefers-color-scheme: dark)').matches
      ? 'dark'
      : 'light';
  }

  setTheme(theme) {
    this.theme = theme;
    document.documentElement.setAttribute('data-theme', theme);
    localStorage.setItem('theme', theme);

    // Update theme toggle state
    const themeToggle = document.querySelector('[data-theme-toggle]');
    if (themeToggle) {
      themeToggle.setAttribute('aria-pressed', theme === 'dark');
      themeToggle.setAttribute(
        'aria-label',
        `Switch to ${theme === 'dark' ? 'light' : 'dark'} theme`
      );
    }
  }

  toggleTheme() {
    this.setTheme(this.theme === 'light' ? 'dark' : 'light');
  }

  // Utility method to check if animations should be disabled
  shouldDisableAnimations() {
    return this.supportsReducedMotion;
  }
}

// Note: ThemeManager is now initialized in main.js to prevent conflicts
